 <template>  
    <div class="container">
        <div class="notes">
            <div :class="{itsagrid:!grid}"  class="note" v-for="(note, index) in notes" :key="index">
                <div class="note-header">
                    <div @click="remove(index)" class="krest">x</div>
                    <p>{{note.title}} </p>
                </div>
                <div class="note-body">
                    <p>{{note.description}}</p>
                    <span>{{note.date}}</span>
                </div>
            </div>
        </div>
    </div>
</template> 
<script> 
    export default{
        props:{
            notes:{
                type: Array,
                required: true,
            },
            grid:{
                type: Boolean,
                required: true,
                default: true,
            }
        },
        methods: {
            remove(index){
                this.$emit('remove', index)
            }
        }
    }
</script>
<style>
    img{
        opacity: .5;
        margin: 10px;
        width: 30px; 
        cursor: pointer;
    }
    .notes{
        display: flex;
        align-items: flex-start;
        width: 40%;
        height: 400px;
        flex-wrap: wrap;
        justify-content: space-around;
    }
    .note{
        position: relative;
        background: rgba(168, 213, 255, 0.5);
        flex: 100%;
        margin: 10px;
        padding: 10px;
        border-radius: 4px;
        transition: .3s all ease-in;
        transition-delay: 0s;
    }
    .note-body span{
        color: rgba(99, 179, 253, 0.849);
        font-size: .8em;
    }
    .note-header{
        color: rgb(151, 132, 255);
        font-size: 1.5em;
    }
    .note:hover{
        box-shadow:1px 0px 14px 0px rgba(94, 108, 136, 0.65);
        transform: translateY(-5px);
    }
    .container{
        width:100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        transition-delay: 0s;
    }
    .krest{
        position: absolute;
        top: 3%;
        right: 2%;
        color:#f8aaff;
        cursor: pointer;
    }
    .itsagrid{
        flex: 30%;
    }
</style>