<template>
    <div class="new-note">
        <label for="title">Название заметки</label>
        <input v-model="note.title" type="text" class="text title">
        <label for="textarea">Описание заметки</label>
        <input v-model="note.description" >
        <button @click="addNote">добавить</button>
    </div>
</template>
<script>
    export default{
        props:{
            note:{
                type: Object,
                required: true
            }
        },
        methods: {
            addNote(){
                this.$emit('addNote', this.note)
            }
        }
    }
</script>
<style>
    input, textarea, select {
        appearance: none;
        border: 1px solid #5e97ad65;
        border-radius: 8px;
        width: 40%;
        height: 3em;
    }
    input,button:focus {
        outline: none;
    }
    label{
        font-size: 2em;
    }
    .new-note{
        font-size: 12px;
        display: flex;
        justify-content: space-around;
        flex-direction: column;
        align-items: center;
        height: 15rem;
    }
    button{
        width: 6em;
        height: 2em;
        cursor: pointer;
        border-radius: 4px;
        border: none;
        background-color: rgb(151, 132, 255);
        color: rgb(221, 255, 249);
    }

</style>