<template>
  <h1></h1>
  <message :message="message"/>
  <newNote :note="this.note" @addNote="addNote"/>
  
  <div class="yts">
    <div class="overnote">
      <p>Твои заметки</p>
      <p>
        <img class="1s" :class="{active:grid}" src="list.png" @click="grid=true">
        <img class="2s" :class="{active:!grid}" src="grid.svg"  @click="grid=false">
      </p>
    </div>
  </div>
  <notes :grid="this.grid" @remove="remove" :notes="notesFilter" />
</template>

<script>

import message from '@/components/message.vue'
import notes from '@/components/Notes.vue'
import newNote from '@/components/NewNote.vue'

  export default{
    components:{
      message, newNote, notes
    },
    data() {
      return {
        title: 'Notes.app',
        search: '',
        note: {
            title:'',
            description: '',
        },
        message: "",
        grid: true,
        notes:[
            {
                title:'1st Note',
                description: 'its about first',
                date: new Date(Date.now()).toLocaleString(),
            },
            {
                title:'2st Note',
                description: 'its about second',
                date: new Date(Date.now()).toLocaleString(),
            },
            {
                title:'3st Note',
                description: 'its about third',
                date: new Date(Date.now()).toLocaleString(),
            }
        ]
      }
    },
    created() {
      if (localStorage.getItem('todo')!=undefined){
         this.notes = JSON.parse(localStorage.getItem('todo'))
      }
    },
    watch:{
      reloading(){
        if (localStorage.getItem('todo')!=undefined){
          this.notes = JSON.parse(localStorage.getItem('todo'))
        }
      }
    },
    
    methods: {
        addNote(){
          let {title, description} = this.note;
          if (title === '') return this.message = 'Введите значения';
          this.notes.push({
              title,
              description,
              date: new Date(Date.now()).toLocaleString(),
          });
          localStorage.setItem('todo',JSON.stringify(this.notes))
          this.note.description="";
          this.note.title="";
          return this.message = false;
        },
        remove(index){
          this.notes.splice(index,1);
          localStorage.setItem('todo',JSON.stringify(this.notes))
        }
    },
    computed:{
      notesFilter(){
        let search = this.search;
        let array = this.notes;
        if(!search){
          return array
        }
        search = search.trim().toLowerCase();
        array = array.filter(function(item){
          console.log(item.title.toLowerCase().indexOf(search))
          if(item.title.toLowerCase().indexOf(search)!==-1){
            return item;
          }
        })
        return array
      }
    },
    
  }
</script>

